<template>
  <div>
    <el-card>
      <div>
        <div class="big-padding">
          <el-table
            :data="tableState"
            border
            style="width: 100%">
            <el-table-column
              prop="success"
              label="程序运行状态"
              width="150">
            </el-table-column>
            <el-table-column
              prop="message"
              label="异常信息">
            </el-table-column>
          </el-table>
        </div>
        <div class="small-padding">程序执行周期占比</div>
        <div class="big-padding">
          <el-progress :text-inside="true" :stroke-width="24" :percentage="percentage" status="success"></el-progress>
        </div>
      </div>
      <div>

      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  data () {
    return {
      percentage: 0,
      tableState: [{ success: '停止', message: '未启动' }]
    }
  },
  created () {
    this.getPercentage()
  },
  methods: {
    async getPercentage() {
      const { data: res } = await this.$http.get('script/status', { params: {} })
      if (!res || !res.success) return this.$message.error('程序状态获取失败')
      this.percentage = parseInt(res.data.cost_time / res.data.all_time * 100)
      this.tableState = [{ success: res.data.success === 1 ? '正常' : '异常', message: res.data.message }]
    }
  }
}
</script>

