<template>
  <div>

    <!-- <el-breadcrumb separator-class="el-icon-arrow-right">
    <el-breadcrumb-item :to="{ path: '/welcome' }">交易系统</el-breadcrumb-item>
    <el-breadcrumb-item>帐号管理</el-breadcrumb-item>
    </el-breadcrumb> -->

  <el-card>

    <el-row>
      <el-col :sapn="4">
        <el-button size="small" type="primary" @click="addAccountVisible = true">添加帐号</el-button>
      </el-col>
    </el-row>

    <el-table
    :data="accountList"
    stripe
    style="width: 100%">
    <el-table-column
      type="index">
    </el-table-column>
    <el-table-column prop="apiKey" label="apiKey"></el-table-column>
    <el-table-column prop="desc" label="备注"></el-table-column>
    <el-table-column prop="platform" label="平台"></el-table-column>
    <el-table-column prop="coinPair" label="交易对"></el-table-column>
    <el-table-column prop="coin2JoinQuantity" label="最少本金币"></el-table-column>
    <el-table-column prop="coin1JoinQuantity" label="持仓判定阙值"></el-table-column>
    <el-table-column prop="timeframe" label="K线时帧"></el-table-column>
    <el-table-column label="运行开关">
      <template slot-scope="scope">
        <el-switch
          v-model="scope.row.state"
          @change="stateChange(scope.row)"
          active-color="#13ce66"
          inactive-color="#ff4949"
          :active-value="1"
          :inactive-value="0">
        </el-switch>
      </template>
    </el-table-column>
    <el-table-column fixed="right" label="操作">
      <template slot-scope="scope">
        <el-button size="mini" type="primary" icon="el-icon-edit" circle @click="showEditDialog(scope.row.id)"></el-button>
      </template>
    </el-table-column>
    </el-table>

  </el-card>

  <!-- 添加帐号对话框 -->
  <el-dialog
    title="添加帐号"
    :visible.sync="addAccountVisible"
    width="50%"
    @close="addAccountClose">
    <el-form ref="addFormRef" :model="addForm" :rules="addFormRules" label-width="120px">
      <el-form-item label="apiKey" prop="apiKey">
        <el-input v-model="addForm.apiKey"></el-input>
      </el-form-item>
      <el-form-item label="secret" prop="secret">
        <el-input v-model="addForm.secret"></el-input>
      </el-form-item>
      <el-form-item label="passphrase">
        <el-input v-model="addForm.passphrase" placeholder="仅okex需要"></el-input>
      </el-form-item>
      <el-form-item label="交易对" prop="coinPair">
        <el-input v-model="addForm.coinPair"></el-input>
      </el-form-item>
      <el-form-item label="备注">
        <el-input v-model="addForm.desc"></el-input>
      </el-form-item>
      <el-form-item label="平台">
        <el-radio-group v-model="addForm.platform">
          <el-radio label="okex"></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="最少本金币" prop="coin2JoinQuantity">
        <el-input v-model="addForm.coin2JoinQuantity"></el-input>
      </el-form-item>
      <el-form-item label="持仓判定阙值" prop="coin1JoinQuantity">
        <el-input v-model="addForm.coin1JoinQuantity"></el-input>
      </el-form-item>
      <el-form-item label="K线时帧">
        <el-radio-group v-model="addForm.timeframe">
          <el-radio label="1d"></el-radio>
          <el-radio label="12h"></el-radio>
          <el-radio label="4h"></el-radio>
          <el-radio label="2h"></el-radio>
          <el-radio label="1h"></el-radio>
        </el-radio-group>
      </el-form-item>
    </el-form>
    <span slot="footer" class="dialog-footer">
      <el-button @click="addAccountVisible = false">取 消</el-button>
      <el-button type="primary" @click="addAccount">确 定</el-button>
    </span>
  </el-dialog>

  <!-- 编辑帐号对话框 -->
  <el-dialog
    title="编辑"
    :visible.sync="editDialogVisible"
    width="50%">
    <el-form ref="addFormRef" :model="editForm" :rules="addFormRules" label-width="120px">
      <el-form-item label="apiKey" prop="apiKey">
        <el-input v-model="editForm.apiKey"></el-input>
      </el-form-item>
      <el-form-item label="secret" prop="secret">
        <el-input v-model="editForm.secret"></el-input>
      </el-form-item>
      <el-form-item label="passphrase">
        <el-input v-model="editForm.passphrase" placeholder="仅okex需要"></el-input>
      </el-form-item>
      <el-form-item label="交易对">
        <el-input v-model="editForm.coinPair"></el-input>
      </el-form-item>
      <el-form-item label="备注">
        <el-input v-model="editForm.desc"></el-input>
      </el-form-item>
      <el-form-item label="平台">
        <el-radio-group v-model="editForm.platform">
          <el-radio label="okex"></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="最少本金币">
        <el-input v-model="editForm.coin2JoinQuantity"></el-input>
      </el-form-item>
      <el-form-item label="持仓判定阙值">
        <el-input v-model="editForm.coin1JoinQuantity"></el-input>
      </el-form-item>
      <el-form-item label="K线时帧">
        <el-radio-group v-model="editForm.timeframe">
          <el-radio label="1d"></el-radio>
          <el-radio label="12h"></el-radio>
          <el-radio label="4h"></el-radio>
          <el-radio label="2h"></el-radio>
          <el-radio label="1h"></el-radio>
        </el-radio-group>
      </el-form-item>
    </el-form>
    <span slot="footer" class="dialog-footer">
      <el-button @click="editDialogVisible = false">取 消</el-button>
      <el-button type="primary" @click="editAccount">确 定</el-button>
    </span>
  </el-dialog>

  </div>

</template>

<script>
export default {
  data () {
    return {
      accountList: [],
      addAccountVisible: false,
      editDialogVisible: false,
      addForm: {
        apiKey: '',
        secret: '',
        passphrase: '',
        platform: 'okex',
        coinPair: '',
        coin2JoinQuantity: '',
        coin1JoinQuantity: '',
        desc: '',
        timeframe: '1d',
        strategy: 'haigui'
      },
      editForm: {},
      addFormRules: { apiKey: [{ required: true, message: '请输入apiKey', trigger: 'blur' }], secret: [{ required: true, message: '请输入secret', trigger: 'blur' }], coinPair: [{ required: true, message: '请输入交易对', trigger: 'blur' }], coin2JoinQuantity: [{ required: true, message: '请输入最小本金币数量', trigger: 'blur' }], coin1JoinQuantity: [{ required: true, message: '请输入持仓币阙值', trigger: 'blur' }] }
    }
  },
  created () {
    this.getAccountList()
  },
  methods: {
    async getAccountList() {
      const { data: res } = await this.$http.get('accounts', { params: {} })
      if (!res || !res.success) return this.$message.error('获取帐号列表失败')
      this.accountList = res.data
    },
    async stateChange(row) {
      const { data: res } = await this.$http.post('account/changeState', { id: row.id, state: row.state })
      if (!res || !res.success) {
        row.state = !row.state
        return this.$message.error('更新状态失败')
      }
    },
    addAccountClose() {
      this.$refs.addFormRef.resetFields()
    },
    addAccount() {
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) return
        const { data: res } = await this.$http.post('account/add', this.addForm)
        console.log(res)
        if (!res) {
          this.$message.error('添加帐号失败')
        }
        this.$message.success('添加帐号成功')
        this.addAccountVisible = false
        this.getAccountList()
      })
    },
    editAccount() {
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) return
        const { data: res } = await this.$http.post('account/edit', this.editForm)
        console.log(res)
        if (!res) {
          this.$message.error('编辑帐号失败')
        }
        this.$message.success('编辑帐号成功')
        this.editDialogVisible = false
        this.getAccountList()
      })
    },
    async showEditDialog(id) {
      this.editDialogVisible = true
      const { data: res } = await this.$http.post('account/query', { id })
      console.log(res)
      if (!res) {
        this.$message.error('查询帐号失败')
      }
      this.editForm = res.data
      this.editDialogVisible = true
    }
  }
}
</script>

<style>
.text {
    font-size: 14px;
  }

  .item {
    padding: 18px 0;
  }

  .box-card {
    width: 480px;
  }
</style>
